.about-text {
    font-family: 'Playfair Display', serif;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
    font-size: 40px;
    text-align: center;
    color: #030d1d;
}

/* Default styles for shop-image */
.about-image {
    max-width: 100%;
    max-height: 300px;
    object-fit: cover;
    background-color: white;
}

/* Default styles for shop-part */
.about-part {
    position: relative;
    text-align: center;
    align-items: center;
    color: white;
    display: flex;
    justify-content: center;
}

.watch-style-part{
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 5rem;
}

.watch-style-image{
    width: 500px;
    height: 500px;
}

.watch-style-text-part{
    display: flex;
    flex-direction: column;
    justify-content: start;
    text-align: start;

}

.watch-style-text-part h1{
    color:white;
    font-family: 'Playfair Display', serif;
    margin-bottom: 1rem;
}

.watch-style-text-part span{
    color:gray;
    font-size: 23px;
    font-weight: 700;
    font-family: 'Source Sans Pro', sans-serif;
    
}

.watch-style-text-part p{
    color:gray;
    font-size: 17px;
    line-height: 1.5;
    margin-top: 1rem;
    font-family: 'Source Sans Pro', sans-serif;
}

.watch-style-text-part ul{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    color:gray;
    line-height: 2.5;
    font-family: 'Source Sans Pro', sans-serif;
    margin-top: 1rem;
    font-size: 20px;
    cursor: pointer;
    
}
.watch-style-text-part ul li{
  
}
.watch-style-text-part ul li:hover{
    color:#CCA471;
   
}




.journey-part{
    text-align: center;
    font-size: 40px;
    font-family: 'Playfair Display', serif;
    color:white;
}




.about-box-container {
    display: grid;
    grid-template-columns: repeat(3, 300px); /* Use fractional units for equal distribution */
    gap: 30px;
    justify-content: center;
    margin-top: 3rem;
    overflow: hidden;
}

.about-box-container div {
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 1px solid  #CCA471;
}

.about-box-container div:hover {
    border: 1px solid gray;
    transition: 0.1s ease all;
}

.about-box-container div .box-icon {
    color: #CCA471;
    font-size: 40px;
}

.about-box-container div span {
    color: white;
    font-family: 'Playfair Display', serif;
    font-size: 25px;
}

.about-box-container div p {
    color: gray;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 17px;
    margin-top: 1rem;
    text-align: center;
}









.hand-made-part {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 4rem;
}

.hand-made-image {
    width: 100%; /* Make the image responsive to the container width */
    max-width: 500px; /* Set a maximum width to maintain image quality */
    height: auto;
}

.hand-made-text {
    margin-top: 2rem;
    text-align: center;
}

.hand-made-text p {
    color: white;
    font-size: 24px;
    font-family: 'Playfair Display', serif;
    margin-bottom: 1rem;
}

.hand-made-text span {
    color: gray;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 16px;
    line-height: 1.5;
}

/* Responsive Styles */
@media screen and (min-width: 768px) {
    .hand-made-part {
        flex-direction: row;
    }

    .hand-made-text {
        margin-left: 4rem;
        text-align: left;
    }
}







/* Responsive Styles */
@media screen and (max-width: 768px) {
    .about-box-container {
        grid-template-columns: repeat(2, 1fr);
            gap: 10px; /* Two columns for medium-sized screens */
    }
}

@media screen and (max-width: 480px) {
    .about-box-container {
        grid-template-columns: 1fr; 
        gap: 10px;/* Single column for small screens */
    }
}















/* Default styles for smaller screens */
@media screen and (max-width: 768px) {
    .about-text {
        font-size: 30px;
    }

    .watch-style-part {
        flex-direction: column;
        align-items: center;
    }

    .watch-style-image {
        width: 100%;
        height: auto;
        max-width: 300px;
    }

    .watch-style-text-part {
        text-align: center;
        align-items: center;
        justify-content: center;
    }
}

/* Additional media query for even smaller screens */
@media screen and (max-width: 480px) {
    .about-text {
        font-size: 24px;
    }

    .journey-part {
        font-size: 40px;
    }
}